.form-control-wrap, .form-control-group { 
	position: relative; }

.form-icon { 
	position: absolute; 
	display: inline-flex; 
	justify-content: center; 
	align-items: center; 
	top: 50%; 
	transform: translateY(-50%); 
	width: calc(1rem + 24px); 
	height: calc(1rem + 24px); }

.form-icon .icon { 
	font-size: 16px; 
	color: #8094ae; }

.form-icon + .form-control { 
	padding-left: calc(1rem + 24px); }

.form-icon-right { 
	left: auto; 
	right: -1px; }

.form-icon-right + .form-control { 
	padding-left: 1rem; 
	padding-right: calc(1rem + 24px); }

.passcode-icon { 
	display: none; }

.passcode-icon.icon-show { 
	display: block; }

.is-shown .passcode-icon.icon-show { 
	display: none; }

.is-hidden .passcode-icon.icon-show { 
	display: block; }

.is-shown .passcode-icon.icon-hide { 
	display: block; }

.is-hidden .passcode-icon.icon-hide { 
	display: none; }