// Initialization of global variables, mixins and functions
@import "../init";

// Theme colors
@each $name, $value in $layout-themes {

    body.skin-#{$name} {
        // Base
        .aside {
            background-color: get($aside-config, base, bg-color);
            .aside-primary {
                background-color: get($value, color1);
                border-right: 1px solid lighten(get($value, border), 35%);
            }
        }    

        // Clean button
        .btn {
            &.btn-clean {
                $btn-background: lighten(get($value, color1), 10%);
                $btn-border: transparent;
                $btn-color: get($value, color);
                $btn-icon: get($value, color);
                $btn-box-shadow: null;
            
                $btn-hover-background: lighten(get($value, color1), 15%);
                $btn-hover-border: transparent;
                $btn-hover-color: get($value, color1);
                $btn-hover-icon: get($value, color);
                $btn-hover-box-shadow: null;
            
                $btn-active-background: lighten(get($value, color1), 30%);
                $btn-active-border: $btn-hover-border;
                $btn-active-color: $btn-hover-color;
                $btn-active-icon: get($value, color);
                $btn-active-box-shadow: $btn-hover-box-shadow;
            
                @include button-custom-variant(
                    $btn-background, $btn-border, $btn-color, $btn-icon, $btn-box-shadow,
                    $btn-hover-background, $btn-hover-border, $btn-hover-color, $btn-hover-icon, $btn-hover-box-shadow,
                    $btn-active-background, $btn-active-border, $btn-active-color, $btn-active-icon, $btn-active-box-shadow
                );
            }

            &.btn-primary {
                $btn-background: get($value, color1);
                $btn-border: get($value, border);
                $btn-color: get($value, color);
                $btn-icon: get($value, color);
                $btn-box-shadow: null;
        
                $btn-hover-background: darken(get($value, color1), 5%);
                $btn-hover-border: get($value, border);
                $btn-hover-color: get($value, color);
                $btn-hover-icon: get($value, color);
                $btn-hover-box-shadow: null;
        
                $btn-active-background: $btn-hover-background;
                $btn-active-border: $btn-hover-border;
                $btn-active-color: $btn-hover-color;
                $btn-active-icon: $btn-hover-icon;
                $btn-active-box-shadow: $btn-hover-box-shadow;
        
                @include button-custom-variant(
                    $btn-background, $btn-border, $btn-color, $btn-icon, $btn-box-shadow,
                    $btn-hover-background, $btn-hover-border, $btn-hover-color, $btn-hover-icon, $btn-hover-box-shadow,
                    $btn-active-background, $btn-active-border, $btn-active-color, $btn-active-icon, $btn-active-box-shadow
                );
            }
        }

        .header {
            .navheader-nav.nav {
                .nav-item {
                    .nav-title, i {
                        color: get($value, color3) !important;
                    }
    
                    &.active,
                    &:hover {
                        border-bottom-color: get($value, color3);
                        .nav-title, i {
                            color: get($value, color3) !important;
                        }
                    }

                    .nav-label {
                        border-right-color: get($value, color3);
                    }
                }
            }
        }

        // Tablet & Mobile Modes
        @include media-breakpoint-down(md) {
            .header-mobile {
                background-color: get($value, color1);
            }
        }

        // Base
        .symbol {
            // Circle style
            &.symbol-users {
                background-color: lighten(get($value, color1), 30%);
            }
        }
        
        .burger-icon {
            @include  burger-icon-theme(get($value, color1), get($value, color1), get($value, color1));
        }

        // Aside
        .login.login-4 .login-aside {
            background: linear-gradient(147.04deg, get($value, color1) 0.74%, get($value, color2) 99.61%);
        }
        
        // Default Theme
        .navi {
            .navi-item {
                .navi-link {

                    &:hover {
                        @include navi-link-theme(get($navi-config, link-bullet-bg-hover), get($value, color3), get($value, color3), get($value, color3), null, null);
                    }

                    &.active {
                        @include navi-link-theme(get($navi-config, link-bullet-bg-active), get($value, color3), get($value, color3), get($value, color3), null, null);
                    }
                }
            }
        }
        
        .datatable.datatable-default {
            // Base
            > .datatable-table {
                > .datatable-body {
                    .datatable-toggle-detail {
                        background-color: get($value, color3) !important;
                    }
                }
            }
        }
    }
        
    .radio{
        &.radio-#{$name} {
            @include radio-solid-theme(lighten(get($value, color2), 20%), get($value, color1), get($value, color2));
        }
        
        // Accent style
        &.radio-accent.radio-#{$name} {
            @include radio-accent-theme(get($value, color3), $gray-400);
        }
    }
}